@import url(https://fonts.googleapis.com/css?family=Rokkitt&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.box {
  font-family: 'Rokkitt', serif;
    background-color: #848fa5bb;
    text-align: center;
    align-items: baseline;
    color: #2e2f2f;
}

.profileImg {
    box-shadow: 1px 1px 2px black;
    border-radius: 5%;
}

.contact {
    color: #e9dab3 !important;
    text-shadow: 1px 1px #2e2f2f86;
}
.contact:hover {
    color: #fdfffc !important;
    text-shadow: 1px 1px rgba(0, 0, 0, 0.671);
}

p {
    font-size: 22px;
}

h5 {
    font-size: 42px;
    font-family: "Roboto Mono", monospace;
    margin: 14px;
}

.intro {
    margin-top: 10px;
    font-size: 60px;
}
.projectPic {
  /* box-shadow: 1px 1px 3px grey; */
  border-bottom: 1px solid rgba(128, 128, 128, 0.322);
  height: 250px;
  width: 500px;
}

.projBox {
  display: flex;
  flex-flow: column wrap;
  align-content: center;
  justify-content: center;
  box-shadow: 1px 1px 3px grey;
  font-family: "Roboto Mono", monospace;
  background: #848fa570;
}

.card {
  display: flex;
  flex-flow: column wrap;
  margin: 10px;
  
}

.text {
  width: 500px;
  text-align: center;
  background-color: #848fa536;
  padding: 5px;
  letter-spacing: 1px;
}

.title {
  text-decoration: underline;
  text-align: center;
  margin-top: 20px;
  font-size: 48px;
}

.tag {
    display: flex !important;
}

.tag:not(body) {
    background-color: #848fa536;
}

a {
  color: white;
}

a:hover {
  color: rgb(86, 121, 141);
}

.anchor {
  text-align: center;
  color: #2e2f2f;
  margin-bottom: 5px;
}

@media only screen and (max-width: 500px) {
    .text {
        width: 100vw;
    }
}

